import { mock } from './mock';
import axios from 'axios';
import { API_ENDPOINT } from '@/constants/endpoints';

export const QUERY_USERS = async ({
  offset,
  limit,
  name,
  email,
}) => {
  const resp = await axios.get(`${API_ENDPOINT}/query_users`, {
    params: {
      offset, limit, name, email
    },
  });
  return resp.data;
}

export const CREATE_USER = async ({
  name,
  email,
  mobile,
  address,
  birthday,
}) => {
  const resp = await axios.post(`${API_ENDPOINT}/create_user`, {
    name,
    email,
    mobile,
    address,
    birthday,
  });
  return resp.data;
}

