const listenChats = async () => {
  const conn = new WebSocket("ws://" + document.location.host + "/ws");
  conn.onclose = (evt) => {
    console.log('close', evt);
  };
  conn.onmessage = (evt) => {
    console.log('message', evt);
  };
  return conn;
}

export const mock = async (route, param) => {
  switch(route) {
    case `${API_ENDPOINT}/query_users`: {
      const resp = await queryUsers(param);
      return resp;
    }
    case `${API_ENDPOINT}/query_transactions`: {
      const resp = await queryTransactions(param);
      return resp;
    }
    case `${API_ENDPOINT}/query_appointments`: {
      const resp = await queryAppointments(param);
      return resp;
    }
    case `${API_ENDPOINT}/query_orders`: {
      const resp = await queryOrders(param);
      return resp;
    }
    case `${API_ENDPOINT}/query_chats`: {
      const resp = await queryChats(param);
      return resp;
    }
    case `${API_ENDPOINT}/listen_chats`: {
      const resp = await listenChats(param);
      return resp;
    }
    default:
      return;
  }
}