import * as user from '@/api/user';
import { toCommaString } from '../../utils/transformer';

export default {
  namespaced: true,
  state: {
    stocks: [],
    counts: {
      stocks: 0,
    },
  },
  getters: {
    stocks: (state) => {
      return state.stocks.map(s => {
        return {
          ...s,
          price: toCommaString(s.price),
        };
      });
    },
  },
  mutations: {
    setStocks: (state, payload) => {
      state.stocks = payload.stocks;
      state.counts.stocks = payload.count;
    },
  },
  actions: {
    queryStocks: async ({ commit }, payload) => {
      const stocks = await user.QUERY_STOCKS(payload);
      commit('setStocks', stocks);
    },
    createStock: async (_, payload) => {
      await user.CREATE_STOCK(payload);
    },
  },
};
