import * as user from '@/api/user';
import { toCommaString } from '@/utils/transformer';

export default {
  namespaced: true,
  state: {
    transactions: [],
    appointments: [],
    orders: [],
    counts: {
      transactions: 0,
      appointments: 0,
      orders: 0,
    },
  },
  getters: {
    transactions: (state) => {
      return state.transactions.map(tr => {
        return {
          ...tr,
          op: '轉點數',
          from_name: tr.from_uuid === 'official' ? '官方' : tr.from_name,
          from_email: tr.from_uuid === 'official' ? '官方' : tr.from_email,
          point: toCommaString(tr.point),
        };
      });
    },
  },
  mutations: {
    setTransactions: (state, payload) => {
      state.transactions = payload.transactions;
      state.counts.transactions = payload.count;
    },
    setAppointments: (state, payload) => {
      state.appointments = payload.appointments;
      state.counts.appointments = payload.count;
    },
    setOrders: (state, payload) => {
      state.orders = payload.orders;
      state.counts.orders = payload.count;
    },
  },
  actions: {
    queryTransactions: async ({ commit }, payload) => {
      const trans = await user.QUERY_TRANSACTIONS(payload);
      commit('setTransactions', trans);
    },
    queryAppointments: async ({ commit }, payload) => {
      const appointments = await user.QUERY_APPOINTMENTS(payload);
      commit('setAppointments', appointments);
    },
    queryOrders: async ({ commit }, payload) => {
      const orders = await user.QUERY_ORDERS(payload);
      commit('setOrders', orders);
    },
  },
};
