import * as user from '@/api/user';
import { toCommaString } from '../../utils/transformer';

export default {
  namespaced: true,
  state: {
    users: [],
    linebots: [],
    telebots: [],
    counts: {
      user: 0,
      linebot: 0,
      telebot: 0,
    },
    dialogs: {
      createUser: false,
      addPoint: false,
      deleteUser: false,
      disableUser: false,
    },
    loadings: {
      user: false,
      linebot: false,
      telebot: false,
    },
    filters: {
      user: {
        name: '',
        email: '',
        limit: 5,
        offset: 0,
      },
      linebot: {
        name: '',
        limit: 5,
        offset: 0,
      },
      telebot: {
        name: '',
        limit: 5,
        offset: 0,
      },
    },
  },
  getters: {
    users: (state) => {
      return state.users.map(u => {
        return {
          ...u,
          point: toCommaString(u.point),
        };
      });
    }
  },
  mutations: {
    setUsers: (state, payload) => {
      state.users = payload.users;
      state.counts.user = payload.count;
    },
    setLinebots: (state, payload) => {
      state.linebots = payload.linebots;
      state.counts.linebot = payload.count;
    },
    setDialog: (state, payload) => {
      state.dialogs[payload] = !state.dialogs[payload];
    },
    setFilter: (state, payload) => {
      state.filters[payload.key] = { ...payload.value };
    },
  },
  actions: {
    queryUsers: async ({ commit, state }) => {
      const users = await user.QUERY_USERS(state.filters.user);
      commit('setUsers', users);
    },
    updateFilter: async({ commit, dispatch }, payload) => {
      commit('setFilter', payload);
      dispatch('queryUsers');
    },
    createUser: async ({ dispatch }, payload) => {
      try {
        await user.CREATE_USER(payload);
        dispatch('queryUsers', payload);
      } catch (err) {
        console.error(err);
      }
    },
    officalPayUser: async ({ dispatch }, payload) => {
      try {
        await user.OFFICIAL_PAY_USER(payload);
        dispatch('queryUsers', payload);
      } catch (err) {
        console.error(err);
      }
    },
    disableUser: async ({ dispatch }, payload) => {
      try {
        await user.DISABLE_USER(payload);
        dispatch('queryUsers', payload);
      } catch (err) {
        console.error(err);
      }
    },
    deleteUser: async ({ dispatch }, payload) => {
      try {
        await user.DELETE_USER(payload);
        dispatch('queryUsers', payload);
      } catch (err) {
        console.error(err);
      }
    },
    queryLinebots: async ({ commit }, payload) => {
      try {
        const linebots = await user.QUERY_LINEBOTS(payload);
        commit('setLinebots', linebots);
      } catch (err) {
        console.error(err);
      }
    },
    createLinebot: async ({ dispatch }, payload) => {
      try {
        await user.CREATE_LINEBOT(payload);
        dispatch('queryLinebots', payload);
      } catch (err) {
        console.error(err);
      }
    },
  },
};
